import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Spinner from '../layout/Spinner';
import ProfileTop from './ProfileTop';
import ProfileAbout from './ProfileAbout';
import ProfileExperience from './ProfileExperience';
import ProfileEducation from './ProfileEducation';
import ProfileGithub from './ProfileGithub';
import DashboardActions from '../dashboard/DashboardActions';
import { getProfileByID } from '../../actions/profile';

const Profile = ({

  profile: { profile, loading },
  auth:{user},
  match,
  getProfileByID
}) => {
  console.log("auth:", user);
  console.log("profile:", profile);
  useEffect(() => {
    getProfileByID(match.params.id);
  }, [getProfileByID, match.params.id]);
if (user && user.experience === null) {
  return (
    <DashboardActions />
  )
} else
  return (
    <Fragment>
      {profile === null ? (
       <p>Please create profile</p>
      ) : (
        <Fragment>
          {/*<Link to='/profiles' className='btn btn-light'>
            Back To Profiles
      </Link>*/}
          {/*auth.isAuthenticated &&
            auth.loading === false &&
            auth.user._id === profile._id && (
              <Link to='/edit-profile' className='btn btn-dark'>
                Edit Profile
              </Link>
            )*/}
          <div className='profile-grid my-1'>
            <ProfileTop profile={profile} />
            <ProfileAbout profile={profile} />
            
            <div className='profile-exp bg-white p-2'>
              <h2 className='text-primary'>Experience</h2>
              {profile.experience.length > 0 ? (
                <Fragment>
                  {profile.experience.map(experience => (
                    <ProfileExperience
                      key={experience._id}
                      experience={experience}
                    />
                  ))}
                </Fragment>
              ) : (
                <h4>No experience credentials</h4>
              )}
            </div>

            <div className='profile-edu bg-white p-2'>
              <h2 className='text-primary'>Bachelors</h2>
              {profile.education.bachelors.length > 0 ? (
                <Fragment>
                { profile.education.bachelors.map(item => (
                  <ProfileEducation
                  education={item}
                />
                ))}
                  
                </Fragment>
              ) : (
                <h4>No education credentials</h4>
              )}

            <h2 className='text-primary'>Masters</h2>
            {profile.education.masters.length > 0 ? (
              <Fragment>
              { profile.education.masters.map(item => (
                <ProfileEducation
                education={item}
              />
              ))}
                
              </Fragment>
            ) : (
              <h4>No education credentials</h4>
            )}
          <h2 className='text-primary'>Doctorate</h2>
          {profile.education.doctorate.length > 0 ? (
            <Fragment>
            { profile.education.doctorate.map(item => (
              <ProfileEducation
              education={item}
            />
            ))}
              
            </Fragment>
          ) : (
            <h4>No education credentials</h4>
          )}
        </div>

            {profile.githubusername && (
              <ProfileGithub username={profile.githubusername} />
            )}
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

Profile.propTypes = {
  getProfileByID: PropTypes.func.isRequired,
  profile: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  profile: state.profile,
  auth: state.auth
});

export default connect(
  mapStateToProps,
  { getProfileByID }
)(Profile);
